@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body{
  margin: 0;
  font-family: "Quicksand";
  background-color: #25283D;
  color: #98DFEA;
}
.img{
  margin-top: 60px;
  height: 350px;
  border-radius: 50%;
}
.name{
  font-size: 50px;
}
.about{
  font-size: 25px;
}
.navbar {
  padding: 20px;
  margin: 0 auto;
  border-bottom: 1px solid #EFD9CE;
}
.skills,.expr,.edu{
  padding-top: 100px;
}
.contacts a{
  padding-left: 20px;
  color: #98DFEA;
}
.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 15px;
  text-decoration: none;
  padding: 0px;
  color:#98DFEA;
}
.navbar a:hover {
  color: #07BEB8;
}
.row{
  padding: 20px;
}
.column {
float:left;
width: 50%;
}
.row:after {
  content: "";
  display: table;
  clear: both;
}
.slideshow {
  margin: 0 auto;
  overflow: hidden;
  max-width: 700px;
}
.slide {
  display: inline-block;
  height: 400px;
  width: 100%;
  border-radius: 40px;
  padding-top: 30px;
}
.slideshowSlider {
  white-space: nowrap;
  transition: ease 1000ms;
  padding-top: 30px;
}
.slideshowDots {
  text-align: center;
}
.slideshowDot {
  display: inline-block;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  cursor: pointer;
  margin: 15px 7px 0px;
  background-color: #c4c4c4;
}
.slideshowDot.active {
  background-color: #6a0dad;
}
.heading{
  color:#98DFEA;
  font-size: 40px;
  background-color: #8F3985;
  border-radius: 2%;
  font-weight: bold;
  width: 90%;
  margin-left: 6%;
}
.card{
  background-color:#c4c4c4 !important;
  padding-top: 8px;
  padding-bottom: 8px;
  color:#25283D;
  border-radius: 5%;
  width: 98%;
  margin-left: 8px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.sk{
  background-color:#c4c4c4 !important;
  padding-top: 8px;
  padding-bottom: 8px;
  color:#25283D;
  border-radius: 5%;
  width: 90%;
  margin-left: 30px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.pr{
  font-size:40px;
}
.btn-group{
  width: 80%;
  margin-left: 10%;
}
.btn-group button {
  border: 1px solid #98DFEA; 
  color:#98DFEA;
  background-color: #25283D;
  padding: 10px 24px; 
  cursor: pointer; 
  float: left; 
  width: 33%;
}

.btn-group button:not(:last-child) {
  border-right: none; 
}
.btn-group:after {
  content: "";
  clear: both;
  display: table;
}
.btn-group button:hover {
  background-color: #c4c4c4;
}
.App {
  text-align: center;
  background-color:#25283D;
}

